import React from 'react'
import './resumeStyle.css'


export function ResumePart({logo, info, institution, year, description}){
    
    return(
        <div className="edu-flex-container">
            <h5>{info}</h5>
            <span>{year}</span>
            <br/>
            <p>{institution}</p>
            <p>{description}</p>
        </div>
    );
}