import React from 'react';
import {Switch, Route} from 'react-router-dom';
import {Home, Contact, AboutMe} from '.'

export function Routes(){
    return(
        
            <Switch>
            <Route exact path ="/">
                <Home/>

            </Route>
            <Route exact path ="/contact">
                <Contact/>
            </Route>
            <Route exact path ="/aboutMe">
                <AboutMe/>
            </Route>
        </Switch>
        
        
    );
}