import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import {ContactPart} from '../component/index'
import address from '../img/icons8-address-40.png'
import phone from '../img/icons8-mobile-phone-48.png'
import email from '../img/icons8-email-50.png'



export function Contact(){
    return(
        <div>
            <h2>Contact</h2>
            <Container fluid="md" className="p-5" >
                <Row>
                    
                    <Col className="d-grid gap-5 justify-content-center"> 
                        <ContactPart title ="PHONE" info="+61 426262212" logo={phone} />
                        <ContactPart title ="EMAIL" info="honpankwok@gmail.com" logo={email}/>
                        <ContactPart title ="ADDRESS" info="U201/ 5 Brodie Spark Drive, Wolli Creek NSW 2205" logo={address}/>
                    </Col>
                    
                    <Col>
                        <div className='map d-grid gap-2 justify-content-center' >
                        <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3310.4633343978867!2d151.15183121588123!3d-33.92920932962954!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x6b12b090f1140887%3A0x8639f2c57b2c8df1!2sLinc%2C%205%20Brodie%20Spark%20Dr%2C%20Wolli%20Creek%20NSW%202205!5e0!3m2!1sen!2sau!4v1626279213867!5m2!1sen!2sau" width="550" height="350"  allowFullScreen="" loading="lazy" title="map"></iframe>
                        </div> 
                    </Col>
                </Row>
            </Container>
            
            
        </div>
    )
}